.Header{
  height: 150px;
  width: 1520px;
  background-image: linear-gradient(#141e30  , #243b55);
  color: white;
  text-align: center;
  line-height: 125px;
  font-size: 20px;
  letter-spacing: 2px;
  font-family: cursive;
  border-bottom: 1px solid white;
}
.menu{
	height: 50px;
	width: 1520px;
	background-color: #141e30;
	}
  ul{
    margin: 0px;
    padding: 0px;
    list-style: none;
    height: 50px;
    background-color: #141e30;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    line-height: 50px;
  }
  li{
    float: left;
  }
  ul li a{
    height: 50px;
    width: 200px;
    text-align: center;
    letter-spacing: 1px;
    display: block;
    padding: 0 10px;
    color: white;
    text-decoration: none;
    transition-duration: 0.5s;
  }
  ul li a:hover{
    background-color: white;
    color: black;
  }
.image-div{
  width: 1520px;
}
.img1{
  float: left;
  border: 1px solid white;
}
.img2{
  float: left;
  border-bottom: 1px solid white;
  border-top: 1px solid white;
}
.img3{
  float: left;
  border: 1px solid white;
}
.content{
  width: 1520px;
}
.cont1{
  height: 400px;
  width: 505px;
  float: left;
  position: relative;
  bottom: 3px;
  border-left: 1px solid white;
  border-right: 1px solid white;
  background-image: linear-gradient(#185a9d , #48b1bf);
}
.cont2{
  height: 400px;
  width: 505px;
  float: left;
  position: relative;
  bottom: 3px;
  background-image: linear-gradient(#185a9d , #48b1bf);
}
.cont3{
  height: 400px;
  width: 505px;
  float: left;
  position: relative;
  bottom: 3px;
  background-color: red;
  border-left: 1px solid white;
  border-right: 1px solid white;
  background-image: linear-gradient(#185a9d , #48b1bf);
}
.h3{
  text-align: center;
  color:  #c4e0e5;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.paragraph{
  font-size: 20px;
  text-align:center;
  color: #FFFFF7;
  font-family: monospace;
}
.paragraph2{
  font-size: 16px;
  text-align:center;
  color: white;
  font-family: monospace;
}
.img-cont-div{
  width: 1518px;
}
.imgcont1{
  float: left;
  border-left: 1px solid white;
  border-right: 1px solid white;
  position: relative;
  bottom: 2px;
}
.imgcont2{
  height: 400px;
  width: 715px;
  float: left;
  position: relative;
  bottom: 2px;
  background-image: linear-gradient(#009245 ,#009245, #FCEE21);
  border-right: 1px solid white;
}
.footer{
  height: 100px;
  width: 1520px;
  float: left;
  position: relative;
  bottom: 4.5px;
  background-color: #141e30;
  text-align: center;
  color: white;
  line-height: 90px;
  font-size: 20px;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

